import "./App.css";
import User from "./components/User";
import Skills from "./components/Skills";
import Profil from "./components/Profil";
import FormationsExperiences from "./components/FormationsExperiences";
import DarkMode from "./components/DarkMode";

import { useRef } from 'react';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import ReportTemplate from './components/ReportTemplate';
import PictureAsPdfIcon from "@material-ui/icons/PictureAsPdf";


function App() {

  const reportTemplateRef = useRef(null);

	const handleGeneratePdf = () => {
    html2canvas(reportTemplateRef.current)
      .then((canvas) => {
        const imgData = canvas.toDataURL('image/png');
        const pdf = new jsPDF({
          orientation: 'landscape',
        });
        const imgProps= pdf.getImageProperties(imgData);
        const pdfWidth = pdf.internal.pageSize.getWidth();
        const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
        pdf.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
        pdf.save('cv_floriane_olivier.pdf');
      });
	};

  return (
      <div className="App" ref={reportTemplateRef}>
        <div className="grid__container">
          <div className="sidebar">
            <div className="actions">

            <button id="toPDF" onClick={handleGeneratePdf}>
              {false && (<PictureAsPdfIcon/>)}
            </button>
              <DarkMode />
            </div>
            <User />
            <Skills />
          </div>
          <div className="main">
            <Profil />
            <FormationsExperiences />
          </div>
        </div>
      </div>
  );
}

export default App;